
.FeedbackPage {
  position: relative;
  margin-top: 60px;
  margin-bottom: 60px;
  overflow-x: hidden;
  z-index: 0;
  text-align: center;
}
.sectionTitle {
  margin-bottom: 30px;
  font-weight: bold;
  color: #507bb4;
  font-size: 36px;
}
.search {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  width: 70%;
  max-width: 600px;
  min-width: 250px;
  padding: 20px 25px;
  font-size: 18px;
}
#searchResult ul {
  font-size: 18px;
  margin: 0px;
  margin-left: auto;
  margin-right: auto;
  padding: 0px;
  width: 70%;
  max-width: 600px;
  min-width: 300px;
  height: 200px;
  max-height: 180px;
  background-color: rgba(219, 222, 222, 0.5);
  overflow-y: scroll;
}
#searchResult ul li {
    cursor: pointer;
    padding: 10px;
}



