
.AppNavBar {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  font-size: 26px;
  text-align: left;
  z-index: 100;
}
nav {
  -webkit-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  border-bottom: solid 1px rgba(0, 0, 0, 0.2);
}
.adujstBar {
  padding-top: 10px;
  padding-bottom: 10px;
}
.brandTitle {
  font-size: 24px;
  font-weight: bold;
  color: #1863c7;
  padding-left: 10px;
  padding-right: 20px;
}
#navbar1 {
  font-weight: bold;
}
#navbar1 ul li {
    font-size: 20px;
    padding-left: 10px;
    padding-right: 20px;
    cursor: pointer;
}
#navbar1 ul li #user-pic {
      top: 5px;
      position: relative;
      display: inline-block;
      background-image: url("/assets/images/profile_placeholder.png");
      background-repeat: no-repeat;
      width: 40px;
      height: 40px;
      background-size: 40px;
      border-radius: 20px;
      cursor: initial;
}
@media only screen and (max-width: 480px) {
#navbar1 ul li #user-pic {
          left: 15px;
}
}
#navbar1 ul li #user-name {
      top: 5px;
      left: -40px;
      position: relative;
      font-size: 12px;
      line-height: 36px;
      padding-right: 10px;
      padding-left: 20px;
      cursor: initial;
}
@media only screen and (max-width: 480px) {
#navbar1 ul li #user-name {
          left: -10px;
}
}



